import React from 'react'
import PropTypes from 'prop-types'
import { Link, graphql, StaticQuery } from 'gatsby'
import {Card, Row, Col, Container } from 'react-bootstrap'
import PreviewCompatibleImage from '../PreviewCompatibleImage'
import './style.sass'

class BlogRoll extends React.Component {
  render() {
    const { data } = this.props
    const { edges: posts } = data.allMarkdownRemark

    return (
      <section className="blog-block" id="blog">
      <Container>
        <div className="section-title text-center">
           <span className="badge badge-primary">BLOGS</span>
           <h2>Blogs We Wrote For You</h2>
           <span className="section-title-line"></span>
        </div>
        <Row>
        {posts &&
          posts.map(({ node: post }) => (
            <Col xs={12} xs={4} key={post.id}>

              <Card className={`card theme-card blog-card ${
                  post.frontmatter.featuredpost ? 'is-featured' : ''
                }`}>
                {post.frontmatter.featuredimage ? (
                  <div className="featured-thumbnail">
                    <PreviewCompatibleImage
                      imageInfo={{
                        image: post.frontmatter.featuredimage,
                        alt: `featured image thumbnail for post ${post.frontmatter.title}`,
                      }}
                    />
                  </div>
                ) : null}
                <Card.Body>
                <span className="badge badge-primary">ANALYTICS</span>
                  <h6><Link
                      className="title has-text-primary is-size-4"
                      to={post.fields.slug}
                    >
                      {post.frontmatter.title}
                    </Link></h6>
                  <Card.Text>
                   <span>{post.frontmatter.date}</span>
                     {post.excerpt}
                  </Card.Text>
                  <div className="blog-author">
                    <p><img className="rounded-circle" src="https://askbootstrap.com/preview/osahan-soft-themez-pro/img/stories/2.png" alt="Card image cap" /> <strong>Black Smith</strong> On October 03, 2019</p>
                 </div>
                </Card.Body>
              </Card>

            </Col>
          ))}
          </Row>
      </Container>
      </section>
    )
  }
}

BlogRoll.propTypes = {
  data: PropTypes.shape({
    allMarkdownRemark: PropTypes.shape({
      edges: PropTypes.array,
    }),
  }),
}

export default () => (
  <StaticQuery
    query={graphql`
      query BlogRollQuery {
        allMarkdownRemark(
          sort: { order: DESC, fields: [frontmatter___date] }
          filter: { frontmatter: { templateKey: { eq: "blog-post" } } }
        ) {
          edges {
            node {
              excerpt(pruneLength: 200)
              id
              fields {
                slug
              }
              frontmatter {
                title
                templateKey
                date(formatString: "MMMM DD, YYYY")
                featuredpost
                featuredimage {
                  childImageSharp {
                    fluid(maxWidth: 120, quality: 100) {
                      ...GatsbyImageSharpFluid
                    }
                  }
                }
              }
            }
          }
        }
      }
    `}
    render={(data, count) => <BlogRoll data={data} count={count} />}
  />
)
